'use client';

import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { Home, LayoutDashboard, Calendar, Users, Menu, X, Heart, GraduationCap, Shield, MessageCircle, Zap, Lightbulb, BookOpen, LogIn, LogOut, UserCircle } from 'lucide-react';
import { useState } from 'react';
import { useAuth } from '@/lib/auth-context';

export default function TopNavigation() {
  const pathname = usePathname();
  const router = useRouter();
  const { user, logout, isAuthenticated } = useAuth();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const handleLogout = () => {
    logout();
    router.push('/login');
  };

  const navItems = [
    { href: '/', label: 'Home', icon: Home },
    { href: '/dashboard', label: 'Dashboard', icon: LayoutDashboard },
    { href: '/calendar', label: 'Calendar', icon: Calendar },
    { href: '/employees', label: 'Employees', icon: Users },
    { href: '/shift-logs', label: 'Shift Logs', icon: BookOpen },
    { href: '/incentives', label: 'Bonus Shifts', icon: Zap },
    { href: '/messages', label: 'Messages', icon: MessageCircle },
    { href: '/suggestions', label: 'Suggestion Box', icon: Lightbulb },
    { href: '/team-culture', label: 'Team Culture', icon: Heart },
    { href: '/learning', label: 'Learning Hub', icon: GraduationCap },
    { href: '/compliance', label: 'Compliance', icon: Shield },
  ];

  // Split navigation into two rows
  const primaryNav = navItems.slice(0, 6); // First 6 items
  const secondaryNav = navItems.slice(6); // Remaining 5 items

  return (
    <nav className="bg-gradient-to-r from-slate-900 via-purple-900 to-slate-900 border-b border-slate-700 sticky top-0 z-40">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Logo and Primary Navigation Row */}
        <div className="flex items-center justify-between h-16 border-b border-slate-700/50">
          {/* Logo */}
          <div className="flex items-center">
            <Calendar className="w-8 h-8 text-blue-400" />
            <div className="ml-2">
              <span className="text-xl font-bold text-white">TeamPulse</span>
              <span className="text-xs text-blue-300 block -mt-1">â„¢</span>
            </div>
          </div>

          {/* Primary Desktop Navigation */}
          <div className="hidden lg:flex items-center space-x-2">
            {primaryNav.map((item) => {
              const isActive = pathname === item.href;
              const Icon = item.icon;
              return (
                <Link
                  key={item.href}
                  href={item.href}
                  className={`flex items-center gap-2 px-4 py-2 rounded-lg font-semibold transition-all ${
                    isActive
                      ? 'bg-gradient-to-r from-blue-500 to-purple-500 text-white shadow-lg'
                      : 'text-slate-300 hover:text-white hover:bg-slate-800'
                  }`}
                >
                  <Icon className="w-5 h-5" />
                  {item.label}
                </Link>
              );
            })}

            {/* User Menu */}
            {isAuthenticated ? (
              <div className="flex items-center gap-2 ml-4 pl-4 border-l border-slate-700">
                <div className="flex items-center gap-2 px-3 py-2 bg-slate-800 rounded-lg">
                  <UserCircle className="w-5 h-5 text-blue-400" />
                  <span className="text-sm text-slate-300">{user?.firstName}</span>
                </div>
                <button
                  onClick={handleLogout}
                  className="flex items-center gap-2 px-4 py-2 rounded-lg font-semibold text-slate-300 hover:text-white hover:bg-slate-800 transition-all"
                >
                  <LogOut className="w-5 h-5" />
                  Logout
                </button>
              </div>
            ) : (
              <div className="flex items-center gap-2 ml-4 pl-4 border-l border-slate-700">
                <Link
                  href="/login"
                  className="flex items-center gap-2 px-4 py-2 rounded-lg font-semibold text-slate-300 hover:text-white hover:bg-slate-800 transition-all"
                >
                  <LogIn className="w-5 h-5" />
                  Login
                </Link>
              </div>
            )}
          </div>

          {/* Mobile menu button */}
          <div className="lg:hidden">
            <button
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="text-slate-300 hover:text-white p-2"
            >
              {mobileMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
            </button>
          </div>
        </div>

        {/* Secondary Navigation Row */}
        <div className="hidden lg:flex items-center justify-center h-12 space-x-2">
          {secondaryNav.map((item) => {
            const isActive = pathname === item.href;
            const Icon = item.icon;
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-2 px-4 py-1.5 rounded-lg font-semibold transition-all text-sm ${
                  isActive
                    ? 'bg-gradient-to-r from-blue-500 to-purple-500 text-white shadow-lg'
                    : 'text-slate-300 hover:text-white hover:bg-slate-800'
                }`}
              >
                <Icon className="w-4 h-4" />
                {item.label}
              </Link>
            );
          })}
        </div>
      </div>

      {/* Mobile Navigation */}
      {mobileMenuOpen && (
        <div className="lg:hidden bg-slate-800 border-t border-slate-700">
          <div className="px-2 pt-2 pb-3 space-y-1">
            {/* User Info on Mobile */}
            {isAuthenticated && user && (
              <div className="flex items-center gap-3 px-3 py-3 mb-2 bg-slate-700/50 rounded-lg">
                <UserCircle className="w-6 h-6 text-blue-400" />
                <div>
                  <p className="text-sm font-semibold text-white">{user.firstName} {user.lastName}</p>
                  <p className="text-xs text-slate-400">{user.email}</p>
                </div>
              </div>
            )}

            {navItems.map((item) => {
              const isActive = pathname === item.href;
              const Icon = item.icon;
              return (
                <Link
                  key={item.href}
                  href={item.href}
                  onClick={() => setMobileMenuOpen(false)}
                  className={`flex items-center gap-3 px-3 py-2 rounded-lg font-semibold transition-all ${
                    isActive
                      ? 'bg-gradient-to-r from-blue-500 to-purple-500 text-white'
                      : 'text-slate-300 hover:text-white hover:bg-slate-700'
                  }`}
                >
                  <Icon className="w-5 h-5" />
                  {item.label}
                </Link>
              );
            })}

            {/* Mobile Auth Buttons */}
            <div className="pt-2 mt-2 border-t border-slate-700">
              {isAuthenticated ? (
                <button
                  onClick={() => {
                    handleLogout();
                    setMobileMenuOpen(false);
                  }}
                  className="flex items-center gap-3 px-3 py-2 w-full rounded-lg font-semibold text-slate-300 hover:text-white hover:bg-slate-700 transition-all"
                >
                  <LogOut className="w-5 h-5" />
                  Logout
                </button>
              ) : (
                <Link
                  href="/login"
                  onClick={() => setMobileMenuOpen(false)}
                  className="flex items-center gap-3 px-3 py-2 rounded-lg font-semibold text-slate-300 hover:text-white hover:bg-slate-700 transition-all"
                >
                  <LogIn className="w-5 h-5" />
                  Login
                </Link>
              )}
            </div>
          </div>
        </div>
      )}
    </nav>
  );
}
